html,body{max-width:100vw;overflow-x:hidden}body{color:#000;background:#fffce3;font-family:var(--font-poppins);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}main{max-width:1920px;position:relative;margin:0 auto;padding:80px 0 0;min-height:100vh}*{box-sizing:border-box;padding:0;margin:0}a{color:inherit;text-decoration:none}button,input[type=submit],input[type=reset],input[type=button]{-webkit-appearance:none;background:none;color:inherit;border:none;padding:0;font:inherit;cursor:pointer;outline:inherit}h1,h2{font-weight:700}h3,h4,h5{font-weight:600}h1{font-size:clamp(3.5rem,2.5rem + 3vw,7.5rem)}h2{font-size:clamp(3rem,2.25rem + 2.5vw,6rem)}@media(max-width: 749px){h2{line-height:110%;font-size:48px}}h3{font-size:clamp(2rem,1.5rem + 1.5vw,2.75rem)}h4{font-size:clamp(1.75rem,1.3rem + 1vw,2rem)}h5{font-size:clamp(1.5rem,1.2rem + .8vw,1.75rem)}h6{font-size:clamp(1.25rem,1.15rem + .4vw,1.5rem)}@media(max-width: 749px){body{font-size:14px}}@media(min-width: 750px){body{font-size:16px}}@media(min-width: 1024px){body{font-size:18px}}@media(min-width: 1800px){body{font-size:20px}}